import React from "react"
import styles from "../featuresHero.module.sass"
import posed from "react-pose"
import workflowIcon from "../../../images/workflow-icon.svg"

const HeroImageContainer = posed.div({
  show: { opacity: 1, delay: 400 },
  hide: { opacity: 0 },
})

class CloudNativeHero extends React.Component {
  state = {
    showImage: false,
  }

  componentDidMount() {
    this.setState({ showImage: true })
  }

  render() {
    return (
      <div className={styles.heroOneRow}>
        <div className={styles.inner}>
          <div className={styles.top}>
            <div className={styles.left}>
              <HeroImageContainer
                pose={!this.state.showImage ? "hide" : "show"}
              >
                <div className={styles.circle} />
                <div className={[styles.circle, styles.circle2].join(" ")} />
                <div className={[styles.circle, styles.circle3].join(" ")} />
                <div className={[styles.circle, styles.circle4].join(" ")} />
                <div className={[styles.circle, styles.circle5].join(" ")} />
                <img src={workflowIcon} alt="" className={styles.mainIcon} />
              </HeroImageContainer>
            </div>

            <div className={styles.right}>
              <h2>Cloud Native Workflow, Made Easy</h2>
              <h5>
                Easily Build, Debug, Deploy, Scale and Monitor your entire
                application.
              </h5>
              <a
                href="https://app.kintohub.com/sign-up"
                className="button white arrow"
              >
                Get Started
              </a>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default CloudNativeHero
