import React from "react"
import styles from "./flowChart.module.sass"
import timelineTriangle from "./timeline-triangle.svg"

class FlowChartSection extends React.PureComponent {
  render() {
    const {
      number,
      title,
      subtext,
      children,
      hasLine,
      isBleeding,
      whiteLine,
      noShadow,
      subtext1,
      link,
      linkTitle,
      linkTitle1,
      link1,
      subtext2,
    } = this.props
    const wrapperStyles = isBleeding
      ? styles.bleedingFlowChartSectionContainer
      : styles.flowChartSectionContainer
    const lineStyles = whiteLine ? styles.whiteLine : styles.line
    const imageContainerStyles = noShadow
      ? styles.imageContainerNoShadow
      : styles.imageContainer
    return (
      <div className={wrapperStyles}>
        <div className={styles.lineAndNumber}>
          {hasLine && (
            <div className={lineStyles}>
              <img src={timelineTriangle} className={styles.triangle} alt="" />
              <img src={timelineTriangle} className={styles.triangle} alt="" />
              <img src={timelineTriangle} className={styles.triangle} alt="" />
            </div>
          )}

          <div className={styles.number}>
            <h4 className="light">{number}</h4>
          </div>
        </div>

        {isBleeding ? (
          <div className={styles.bleedingContent}>{children}</div>
        ) : (
          <>
            <div className={styles.text}>
              <h3>{title}</h3>

              <h5>
                {subtext}
                {link ? <a href={link}>{linkTitle} </a> : ""}
                {subtext1 ? subtext1 : ""}
                {link1 ? <a href={link1}> {linkTitle1} </a> : ""}
                {subtext2 ? subtext2 : ""}
              </h5>
            </div>
            <div className={imageContainerStyles}>{children}</div>
          </>
        )}
      </div>
    )
  }
}

export default FlowChartSection
