import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"
import Layout from "../components/layout"
import SEO from "../components/seo"
import styles from "../pageStyles/features.module.sass"
import landingStyles from "../pageStyles/landingPage.module.sass"
import FlowChartSection from "../components/flowChart/FlowChartSection"
import CloudNativeHero from "../components/features/cloudNative/CloudNativeHero"
import ContactFormContainer from "../containers/ContactFormContainer"
import workflowSyncImage from "../images/workflow-sync.svg"
import workflowLogsRequestsImage from "../images/workflow-requests-logs.svg"
import workflowLogsImage from "../images/workflow-requests-logs.svg"
import workflowRequestsImage from "../images/workflow-requests.svg"
import workflowPrometheusGrafana from "../images/workflow-prometheus-grafana.svg"
import workflowPhoneNotificationSVG from "../images/workflow-phone-notification.svg"
import microservice from "../images/icon-kintoblock-type-microservice.svg"
import tasks from "../images/icon-kintoblock-type-tasks.svg"
import custom from "../images/icon-kintoblock-type-custom-services.svg"
import websites from "../images/icon-kintoblock-type-websites.svg"

class CloudNativePage extends React.Component {
  render() {
    const { data } = this.props
    return (
      <Layout>
        <SEO
          title="Cloud Native Workflow"
          keywords={[`kintohub`, `cloud`, `native`]}
        />
        <CloudNativeHero />
        <div className={styles.cloudNative}>
          <div className={landingStyles.flowChart}>
            <FlowChartSection
              number="1"
              title="Automation by Default"
              subtext={[
                "Simply hookup your git service and begin building. Git is a 1",
                <sup>st</sup>,
                " class citizen on KintoHub ensuring branches, tags and pull requests (coming soon) are synced & configurable.",
                ]}
                hasLine={true}
                >
                <Img
                  fluid={data.workflowConnectGit.childImageSharp.fluid}
                  className="rounded-corner-8"
                />
                </FlowChartSection>
                <FlowChartSection number="2" hasLine={true} isBleeding={true}>
                  <div className={styles.addYourServices}>
                    <div className={styles.addYourServicesTop}>
                      <h3>Add Your Services</h3>
                      <h5>
                        KintoBlocks are deployable packages that connect to a
                        specific repository on your git host provider. We monitor
                        changes and build commits automatically. We have created
                        several types of blocks which make up an entire application.
                        From microservices to cron jobs, you can create reusable
                        combinations of blocks that can be shared across your
                        products.
                      </h5>
                    </div>
                    <div className={styles.addYourServicesBottom}>
                      <div className={styles.addYourServicesBottomLeft}>
                        <div className={styles.addYourServicesSection}>
                          <div className={styles.addYourServicesMiniIcon}>
                            <img src={microservice} alt="" />
                            <h4>Microservices</h4>
                          </div>
                          <h5>
                            Lightweight stateless services which perform logic for
                            your business. By default, KintoHub’s microservices are
                            serverless but have the option to be Always On as well.
                          </h5>
                        </div>
                        <div className={styles.addYourServicesSection}>
                          <div className={styles.addYourServicesMiniIcon}>
                            <img src={tasks} alt="" />
                            <h4>Tasks</h4>
                          </div>
                          <h5>
                            Tasks are containers that respond to events on the
                            KintoHub platform. They can be a cron job that runs on a
                            schedule, or a CI/CD event that backs up a database,
                            runs an E2E test or more. The choice is yours!
                          </h5>
                        </div>
                        <div className={styles.addYourServicesSection}>
                          <div className={styles.addYourServicesMiniIcon}>
                            <img src={custom} alt="" />
                            <h4>custom services &amp; data</h4>
                          </div>
                          <h5>
                            Custom services are usually stateful services that
                            require Persistent Storage Disks such as databases like
                            MongoDB, shared memory like Redis or eventing like Kafka.
                          </h5>
                        </div>
                        <div className={styles.addYourServicesSection}>
                          <div className={styles.addYourServicesMiniIcon}>
                            <img src={websites} alt="" />
                            <h4>websites</h4>
                          </div>
                          <h5>
                            Easily host static, dynamic or server-side rendered
                            websites and deploy them alongside your other
                            KintoBlocks.
                          </h5>
                        </div>
                      </div>
                      <div className={styles.addYourServicesImage}>
                        <div className={styles.addYourServicesImageWrapper}>
                          <Img
                            fluid={data.workflowServices.childImageSharp.fluid}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </FlowChartSection>

                <FlowChartSection number="3" hasLine={true} isBleeding={true}>
                  <div className={styles.seeYourUpdates}>
                    <div>
                      <h3>Automatically See Your Updates</h3>
                      <h5>
                        Continuous Deployment comes out of the box with KintoHub.
                        You can map your KintoBlock’s branches into a deployment so
                        that all changes will automatically update once you push to
                        your git repository!
                      </h5>
                    </div>
                    <img
                      src={workflowSyncImage}
                      alt=""
                      className={styles.seeYourUpdatesImage}
                    />
                  </div>
                </FlowChartSection>
                <FlowChartSection number="4" hasLine={true} isBleeding={true}>
                  <h3>Advanced Debugging</h3>
                  <div className={styles.advancedDebugging}>
                    <h5>
                      Debugging potentially 10’s of scaling services is not easy.
                      KintoHub provides real-time logs and requests information to
                      understand issues with your application from dev to
                      production. This starts with quickly analyzing logs on
                      KintoHub’s UI. For advanced analytics and finding bottlenecks
                          with tracing, KintoHub is compatible with Kibana and Jaeger{" "}
                          <b>(coming soon)</b> as well! Kintohub’s CLI tools (in
                          development) help you debug parts of your application locally
                          within your IDE so that you can easily resolve your problem.
                        </h5>
                <div>
                  <Img fluid={data.kibanaJaeger.childImageSharp.fluid} />
                </div>
              </div>
              <div>
                <img
                  src={workflowLogsRequestsImage}
                  alt=""
                  className={styles.desktopImage}
                />
                <img
                  src={workflowLogsImage}
                  alt=""
                  className={styles.mobileImage}
                />
                <img
                  src={workflowRequestsImage}
                  alt=""
                  className={styles.mobileImage}
                />
              </div>
            </FlowChartSection>
            <FlowChartSection
              number="5"
              hasLine={true}
              whiteLine={true}
              isBleeding={true}
            >
              <h3>Monitor Everything</h3>
              <div className={styles.advancedDebugging}>
                <h5>
                  Monitor everything with Prometheus and Grafana to understand
                  how your application is performing. Watch your application
                  scale and find bottlenecks between calls to understand your
                  limitations before it's too late. With Prometheus, you will be
                  able to set up email alerting for your teams to respond to
                  issues with your environment.
                </h5>
                <img src={workflowPrometheusGrafana} alt="" />
              </div>
              <img
                src={workflowPhoneNotificationSVG}
                alt=""
                className={styles.workflowPhoneNotificationSVG}
              />
            </FlowChartSection>
          </div>
        </div>
        <ContactFormContainer />
      </Layout>
    )
  }
}

export default CloudNativePage

export const pageQuery = graphql`
  query {
    workflowConnectGit: file(
      relativePath: { eq: "workflow-connect-git@2x.png" }
    ) {
      ...fluidImage
    }
    workflowPackages: file(relativePath: { eq: "workflow-packages@2x.png" }) {
      ...fluidImage
    }
    workflowServices: file(
      relativePath: { eq: "workflow-add-services@2x.png" }
    ) {
      ...fluidImage
    }
    kibanaJaeger: file(relativePath: { eq: "workflow-kibana-jaeger@2x.png" }) {
      ...fluidImage
    }
  }
`
